/**
 *
 *    URL 
 *
 *  [script  ]
 *  //  alert 
 *  window.copyLink()
 *
 *  // callback alert 
 *  window.copyLink(() => {
 *    alert("")
 *  })
 *
 */

window.copyLink = function (_alert) {
  navigator.clipboard.writeText(window.document.location.href).then(function () {
    if (_alert) _alert();
    else alert("URL .");
  });
};
