<template>
  <section class="portfolio l-section">
    <div class="inner">
      <h3 class="l-section__title"> ESG   </h3>
      <p class="l-section__description">
         ESG       .<br />
        ,   ESG  (Perceived)  .
      </p>
      <!-- Add Pagination -->
      <div class="swiper-pagination"></div>

      <!-- swiper -->
      <div class="swiper" data-loding-spinner="true">
        <div class="swiper-wrapper">
          <section class="swiper-slide" v-for="categorys in datas">
            <ul class="portfolio-list" v-if="categorys">
              <li class="portfolio-list__item" v-for="item in categorys">
                <div class="portfolio-list-thumb">
                  <img class="thumb" :src="item.imgUrl" @error="onError" alt="" />
                </div>
                <div class="portfolio-list-info">
                  <p class="tag" :data-type="item.ib_type_name">{{ item.ib_type_name }}</p>
                  <p class="tit clamp-1">{{ item.ib_title }}</p>
                  <p class="desc clamp-2">{{ item.ib_content }}</p>
                </div>
              </li>
            </ul>
          </section>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";

export default {
  name: "portfolio",
  data() {
    return {
      categorys: ["All"],
      datas: null,
    };
  },
  created() {
    this.getData(() => {
      this.appendSwiper();
    });
  },
  methods: {
    /**
     *
     * API
     *
     */
    getData(_callback) {
      //
      axios
        .get(`${SERVER.api}/api/insight/getInsightTypeCode`)
        .then((_response) => _response.data.insightCode.forEach((_arr) => this.categorys.push(_arr.code_name)))
        .catch((_error) => console.log(_error))
        .then(() => {
          // 
          const types = this.categorys.map((_category, _idx) => axios.post(`${SERVER.api}/api/insight/getInsightInfo?ib_type=${_idx}`));

          axios
            .all(types)
            .then(
              axios.spread((..._response) => {
                this.datas = _response.map((_list) => _list.data.insightInfo.filter((_item) => (_item.imgUrl = SERVER.api + _item.imgUrl)));
                _callback && _callback();
              })
            )
            .catch((_error) => console.log(_error))
            .finally(() => this.$el.querySelector("[data-loding-spinner]").setAttribute("data-loding-spinner", false));
        });
    },

    /**
     *
     * swiper 
     *
     */
    appendSwiper() {
      const $swiper = this.$el.querySelector(".swiper");

      this.swiper = new Swiper($swiper, {
        allowTouchMove: false,
        autoHeight: true,
        effect: "fade",
        pagination: {
          el: this.$el.querySelector(".swiper-pagination"),
          clickable: true,
          renderBullet: (index, className) => {
            return `<button class=" ${className} js-anchor" data-anchor=".js-anchor-tab-point" data-category="${this.categorys[index]}" type="button" ><span class="txt">${this.categorys[index]}</span></button>`;
          },
        },
      });
    },

    /**
     *
     *  
     *
     */
    onError(e) {
      e.target.src = require("@/assets/images/insight/onerror_portfolio_thumb.jpg");
    },
  },
};
</script>

<style lang="scss" scoped>
.portfolio {
  padding-bottom: 5rem;
  background-color: #f0f4f6;
  &-list {
    $list: &;

    padding: 5rem;
    &:empty {
      &::after {
        content: " .";
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        height: 27rem;
        margin-bottom: 3rem;
        padding: 0 3rem;
        border-radius: $G-box-radius;
        font-size: 3rem;
        color: #ccc;
        font-weight: bold;
        background-color: #fff;
        box-shadow: 0 0.2rem 1em rgba(7, 7, 7, 0.08);
      }
    }

    &__item {
      display: flex;
      align-items: center;
      overflow: hidden;
      height: 27rem;
      margin-bottom: 3rem;
      padding: 0 3rem;
      border-radius: $G-box-radius;
      background-color: #fff;
      box-shadow: 0 0.2rem 1em rgba(7, 7, 7, 0.08);
      #{$list}-thumb {
        margin-right: 3rem;
        .thumb {
          width: 31rem;
          height: 21rem;
          border-radius: 0.6em;
        }
      }
      #{$list}-info {
        width: 75rem;
        .tag {
          display: inline-flex;
          align-items: center;
          height: 3rem;
          padding: 0 2.4rem;
          margin-bottom: 1.5rem;
          border-radius: 2em;
          font-size: 1.5rem;
          font-weight: bold;
          line-height: 1em;
          color: #fff;
          background-color: #ccc;

          $types: "Report" #5064ac, "Case Study" #7664df, "View" #4dcdd6;

          @each $type, $color in $types {
            &[data-type="#{$type}"] {
              background-color: $color;
            }
          }
        }
        .tit {
          margin-bottom: 2.2rem;
          font-size: 3.3rem;
          font-weight: bold;
          line-height: 1em;
        }
        .desc {
          height: 8rem;
          font-size: 2.5rem;
          font-weight: 500;
          color: #808080;
          word-break: keep-all;
        }
      }
      @for $i from 0 through 3 {
        &:nth-child(#{$i}) {
          opacity: 0;
          transform: translateY(-20%);

          .swiper-slide-active & {
            opacity: 1;
            transform: translateY(0);
            @include transition($time: 1, $delay: #{$i * 0.1});
          }
        }
      }
    }
  }
}
:deep() .swiper {
  margin: 0 -5rem;
  &-wrapper {
    &:empty {
      &::after {
        [data-loding-spinner="false"] & {
          content: " .";
          display: flex;
          justify-content: center;
          align-items: center;
          width: inherit;
          height: 30rem;
          font-size: 3rem;
          font-weight: bold;
          color: #ccc;
          text-align: center;
        }
      }
    }
  }
  &-pagination {
    all: unset;
    display: flex;
    justify-content: space-between;
    width: auto !important;
    margin: 0 -0.8rem;
    &-bullet {
      $color: #325cf3;

      overflow: hidden;
      position: relative;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      flex: 1;
      position: relative;
      z-index: 1;
      width: auto;
      height: 9rem;
      margin: 0 0.8rem !important;
      padding: 0 2.8rem;
      font-size: 2.6rem;
      color: $color;
      border-radius: 2em;
      font-weight: bold;
      color: $color;
      background-color: #fff;
      opacity: 1;
      @include transition;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        z-index: 0;
        border: 0.3rem solid $G-color-primary;
        border-radius: 2em;
      }
      &::after {
        content: "";
        position: absolute;
        top: -10px;
        bottom: -10px;
        left: -10px;
        right: 100%;
        z-index: -1;
        @include transition;
      }
      &:hover,
      &-active {
        color: #fff;
        box-shadow: 0 0.3rem 1.7rem rgba(46, 43, 176, 0.4);
        &::before {
          border-color: transparent;
          @include transition($time: 0.25);
        }
        &::after {
          right: -10px;
          background: $color;
        }
      }
      &[data-category="All"] {
        display: none;
      }
    }
  }
  &-slide {
    opacity: 0;
  }
}
</style>
