<template>
  <section class="send-mail l-section">
    <div class="inner">
      <h3 class="l-section__title"></h3>
      <p class="l-section__description">P.ESG       .</p>
      <form class="form" @submit="checkForm">
        <fieldset class="form-fieldset" data-fieldset="">
          <h4 class="form-fieldset__title"> </h4>
          <div class="form-row form-text w-50" data-row="">
            <label for="name" class="form-text__label">
              <span class="txt"></span>
              <span class="error" v-if="errors.name">{{ errors.name }}</span>
            </label>
            <input id="name" class="form-text__input" type="text" v-model="forms.name" placeholder=" " />
          </div>
          <div class="form-row form-text w-50" data-row="">
            <label for="phone" class="form-text__label">
              <span class="txt"></span>
              <span class="error" v-if="errors.phone_nm">{{ errors.phone_nm }}</span>
            </label>
            <input id="phone" class="form-text__input" type="text" v-model="forms.phone_nm" placeholder=") 010-1234-5678" />
          </div>
          <div class="form-row form-text w-50" data-row="">
            <label for="corp" class="form-text__label">
              <span class="txt">  </span>
              <span class="error" v-if="errors.group_name">{{ errors.group_name }}</span>
            </label>
            <input id="corp" class="form-text__input" type="text" v-model="forms.group_name" placeholder="   " />
          </div>
          <div class="form-row form-text w-50" data-row="">
            <label for="position" class="form-text__label">
              <span class="txt"> </span>
              <span class="error" v-if="errors.email">{{ errors.email }}</span>
            </label>
            <input id="position" class="form-text__input" type="text" v-model="forms.email" placeholder=") pesg123@prone.com" />
          </div>
          <div class="form-row form-sele w-50" data-row="">
            <label for="job" class="form-text__label">
              <span class="txt"></span>
              <span class="error" v-if="errors.industry_name">{{ errors.industry_name }}</span>
            </label>
            <select class="form-select__select" v-model="forms.industry_name">
              <option disabled value="">  </option>
              <option v-for="code in industryCodes" :value="code.code_name">{{ code.code_name }}</option>
            </select>
          </div>
          <div class="form-row form-text w-100" data-row="" style="height: 30rem">
            <label for="textarea" class="form-text__label">
              <span class="txt"></span>
              <span class="error" v-if="errors.content">{{ errors.content }}</span>
            </label>
            <!-- prettier-ignore -->
            <fieldset class="form-fieldset w-100" data-fieldset="">
              <div class="form-row form-check mr-15" data-row="report" v-for="(code, idx) in inquiryCodes" :data-row="code.code_name">
                <input :id="'checkboxInquiry'+ idx" class="form-check__input" type="checkbox" :value="code.code_name" v-model="forms.inquiry_name"/>
                <label :for="'checkboxInquiry'+ idx" class="form-check__label"><span class="txt">{{code.code_name}}</span></label>
              </div>
            </fieldset>
            <textarea id="textarea" class="form-text__textarea" v-model="forms.content" placeholder="  "></textarea>
          </div>
        </fieldset>
        <fieldset class="form-fieldset" data-fieldset="">
          <div class="form-row form-check" data-row="">
            <input id="agreement" class="form-check__input" type="checkbox" v-model="forms.consent" />
            <label for="agreement" class="form-check__label">
              <span class="txt">
                []         <strong>[]</strong>     .
              </span>
              <br />
              <span class="error" v-if="errors.consent">({{ errors.consent }})</span>
            </label>
          </div>
        </fieldset>
        <div class="btn-area text-center pt-65">
          <button class="btn btn-round-grad btn-round-grad--xlg w-40 js-submit" type="submit">
            <span class="txt"></span>
          </button>
        </div>
      </form>
    </div>
    <ModalAlert @getShow="modalComplete" :isShow="isCompleteModal" :frameset="'#container'">
      <template slot="message"> .</template>
    </ModalAlert>
    <ModalAlert @getShow="modalFailed" :isShow="isFailedModal" :frameset="'#container'">
      <template slot="message"> .  .</template>
    </ModalAlert>
  </section>
</template>

<script>
import axios from "axios";
import ModalAlert from "../../components/modal/ModalAlert.vue";

export default {
  name: "send-mail",
  components: {
    ModalAlert,
  },
  data() {
    return {
      isOnLoad: true,
      isCompleteModal: false,
      isFailedModal: false,
      industryCodes: null,
      inquiryCodes: null,
      errors: new Object(),
      forms: {
        consent: false,
        name: null,
        phone_nm: null,
        group_name: null,
        position: null,
        email: null,
        industry_name: "",
        inquiry_name: [],
        content: null,
      },
    };
  },
  updated() {
    this.isOnLoad && this.onLoadType();
  },
  created() {
    this.getIndustryCode();
    this.getInquiryCode();
  },
  methods: {
    /* URLSearch -  */
    onLoadType() {
      const str = location.hash.replace(/\#\/contact\?/i, "");
      const isParam = new URLSearchParams(str).getAll("sendmail")[0];
      const $rows = this.$el.querySelectorAll("[data-fieldset=] .form-row");

      if (isParam && $rows.length > 0) {
        this.isOnLoad = false;
        $rows.forEach((_$el) => _$el.getAttribute("data-row") === isParam && this.forms.inquiry_name.push(isParam));
      }
    },

    /* API -    */
    getInquiryCode() {
      axios
        .get(`${SERVER.api}/api/contact/inquiry/getInquiryTypeCode`)
        .then((_response) => (this.inquiryCodes = _response.data.inquiryCode))
        .catch((_error) => console.log(_error));
    },

    /* API -    */
    getIndustryCode() {
      axios
        .get(`${SERVER.api}/api/contact/inquiry/getIndustryCode`)
        .then((_response) => (this.industryCodes = _response.data.industryCode))
        .catch((_error) => console.log(_error));
    },

    /* API -    */
    sendMail() {
      axios
        .post(`${SERVER.api}/api/contact/inquiry/send-mail`, {
          i_name: this.forms.name,
          i_phone_nm: this.forms.phone_nm,
          i_group_name: this.forms.group_name,
          i_email: this.forms.email,
          i_industry_name: this.forms.industry_name,
          i_content: this.forms.content,
          i_inquiryType_name: this.forms.inquiry_name.toString(),
        })
        .then((_response) => this.onLoading(false, () => (this.isCompleteModal = true)))
        .catch((_error) => this.onLoading(false, () => (this.isFailedModal = true)));
    },

    /*   */
    checkForm(e) {
      e.preventDefault();
      this.errors = new Object();
      // 
      if (!this.forms.name) this.errors.name = " .";

      // 
      if (!this.forms.phone_nm) this.errors.phone_nm = " .";
      else if (!this.validPhone(this.forms.phone_nm)) this.errors.phone_nm = "  . (: 010-1234-5678)";

      // 
      if (!this.forms.group_name) this.errors.group_name = "   .";

      // 
      if (!this.forms.email) this.errors.email = " .";
      else if (!this.validEmail(this.forms.email)) this.errors.email = "  .";

      // 
      if (!this.forms.industry_name) this.errors.industry_name = " .";

      // 
      if (!this.forms.content) this.errors.content = " .";

      // 
      if (!this.forms.consent) this.errors.consent = " .";

      // submit
      if (Object.keys(this.errors).length === 0) {
        this.sendMail();
        this.onLoading(true);
      }
    },

    /*   */
    validEmail(_email) {
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/; //prettier-ignore
      return re.test(_email);
    },

    /*   */
    validPhone(_phone) {
      const re = /^[0-9]{2,3}-[0-9]{3,4}-[0-9]{4}/;
      return re.test(_phone);
    },

    /* loading */
    onLoading(_boolean, _callback) {
      const $wrap = document.querySelector("#wrap");
      if (_boolean) {
        window.lockBody(); //body scroll lock
        $wrap.setAttribute("data-loding-spinner", "true dimmed fixed"); // lading dimmed remove
      } else {
        window.unlockBody(); //body scroll unlock
        $wrap.setAttribute("data-loding-spinner", "false dimmed fixed"); // lading dimmed remove
      }

      if (_callback) setTimeout(() => _callback(), 800);
    },

    /* Modal -  */
    modalComplete(_boolean) {
      this.isCompleteModal = _boolean;
    },

    /* Modal -  */
    modalFailed(_boolean) {
      this.isFailedModal = _boolean;
    },
  },
};
</script>

<style lang="scss" scoped>
.send-mail {
  background-color: #f8fafc;
}
.form {
  $fo: form;

  padding: 4.5rem 8rem 4.8rem;
  border-radius: $G-box-radius;
  background-color: #fff;
  box-shadow: $G-box-shadow;
  &-fieldset {
    $fi: &;

    &[data-fieldset=""] {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -1.75rem;
      #{$fi}__title {
        flex-basis: 100%;
        margin-bottom: 3.6rem;
        padding-left: 1.75rem;
        font-size: 3.2rem;
        font-weight: bold;
      }
      > .#{$fo}-row {
        height: 7.8rem;
        margin-bottom: 2.5rem;
        padding: 3.2rem 1.75rem 0;
        .form-text__label {
          display: flex;
          align-items: center;
          position: absolute;
          top: 0;
          font-size: 1.8rem;
          .txt {
            margin-right: 1rem;
            &::after {
              content: "*";
              display: inline-block;
              padding-left: 0.3rem;
              color: #f24e1e;
            }
          }
          .error {
            font-size: 1.2rem;
            color: #f24e1e;
            line-height: 1em;
          }
        }
      }
    }
    &[data-fieldset=""] {
      position: absolute;
      top: 3rem;
      left: 2rem;
      padding: 1rem 0;
      @at-root .#{$fo} [data-row=""] {
        padding-top: 8rem !important;
      }
    }
    &[data-fieldset=""] {
      .#{$fo}-row {
        &[data-row=""] {
          .form-check__input {
            border-radius: 100%;
            background-size: 50%;
          }
          .form-check__label {
            strong {
              text-decoration: underline;
            }
          }
          .error {
            position: absolute;
            top: 2.8rem;
            left: 0;
            font-size: 1.2rem;
            color: #f24e1e;
          }
        }
      }
    }
  }
}
</style>
