/* Router */
import Vue from "vue";
import Router from "vue-router";

//MAIN
import Main from "@/views/Main";
import Index from "@/views/Index";
import Dashboard from "@/views/Dashboard";
import Contact from "@/views/Contact";
import Insight from "@/views/Insight";

Vue.use(Router);

const router = new Router({
  // mode: "history",
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  routes: [
    {
      path: "*",
      redirect: "/404",
      //   NotFound   
      // component: NotFound
    },
    {
      path: "/",
      name: "Main",
      component: Main,
    },
    {
      path: "/index",
      name: "Index",
      component: Index,
    },
    {
      path: "/dashboard",
      name: "Dashboard",
      component: Dashboard,
    },
    {
      path: "/insight",
      name: "Insight",
      component: Insight,
    },
    {
      path: "/contact",
      name: "Contact",
      component: Contact,
    },
  ],
});

