/**
 *
 * SPA 404  router 
 *
 */
router.redirect404 = function () {
  const isRedirect = new URLSearchParams(location.search).get("404");

  if (isRedirect) {
    const path = isRedirect.match(/(?<="\s*).*?(?=\s*")/gs).toString();

    router.push(path);
  }
};
router.redirect404();

export default router;
