<template>
  <section class="partner">
    <div class="inner">
      <h3 class="partner__title"><span class="ff-Jost">P.ESG</span>  / </h3>
      <div class="swiper">
        <ul class="swiper-wrapper">
          <li class="swiper-slide swiper-lazy" v-for="data in this.datas"">
            <img class="logo" :src="data.img" :alt="data.title">
            <!-- <i class="swiper-lazy-preloader"></i> -->
          </li>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "partner",
  data() {
    return {
      swiper: null,
      datas: [
        { title: "", img: require("@/assets/images/main/partner_logo/sec.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/kolonindustries.png") },
        { title: "gs", img: require("@/assets/images/main/partner_logo/gs.png") },
        { title: "lg", img: require("@/assets/images/main/partner_logo/leg.png") },
        { title: "s-oil", img: require("@/assets/images/main/partner_logo/soil.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/nongshim.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/dkpharm.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/samsunglife.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/shinsegae.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/ckdhc.png") },
        { title: "cj", img: require("@/assets/images/main/partner_logo/cj.png") },
        { title: "cgv", img: require("@/assets/images/main/partner_logo/cgv.png") },
        { title: "kb", img: require("@/assets/images/main/partner_logo/kb.png") },
        { title: "sk", img: require("@/assets/images/main/partner_logo/skhynix.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/naver.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/koreaair.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/doosanenerbility.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/bing.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/shinhangroup.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/amorepacific.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/ahnlab.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/yuhan.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/kolonindustries.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/posco.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/hybecorp.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/kepco.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/hanssem.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/kepco-enc.png") },
        { title: "", img: require("@/assets/images/main/partner_logo/hyundai-steel.png") },
      ],
    };
  },
  mounted() {
    this.appendSwiper();
    this.resetSwiper();
  },
  methods: {
    appendSwiper() {
      /* swiper */
      const $swiper = this.$el.querySelector(".swiper");
      this.swiper = new Swiper($swiper, {
        autoplay: {
          delay: 1,
          disableOnInteraction : false,
        },
        speed: 3000,
        slidesPerView: 6,
        spaceBetween : 30,
        centeredSlides: true,
        // grid: {
        //   rows: 2,
        //   fill: 'row',
        // },
        loop: true,
        loopedSlides: $swiper.querySelectorAll(".swiper-slide").length,
        allowTouchMove: true,
        // breakpointsInverse: true,
        // breakpoints: {
        //     1023: {
        //         allowTouchMove: false
        //     }
        // },
        // lazy: true,
        // lazy: {
        //   loadPrevNext: false,
        //   loadOnTransitionStart: true,
        // },
        on : {
          init() {
            this.$wrapperEl[0].style = "transition-timing-function:linear"
          }
        }
      });
    },
    resetSwiper() {
      // swiper  
      this.swiper.autoplay.stop();

      $.scrollAction({
        $target: this.$el,
        top: 100,
        scrollDownAction: () => {
          //  DOWN 
          this.swiper.autoplay.start();
        },
        scrollUpAction: () => {
          //  UP 
          this.swiper.slideTo(0, 0, false);
          this.swiper.autoplay.stop();
        },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.partner {
  padding: 10rem 0 20rem;
  .inner {
    width: 132rem;
  }
  &__title {
    font-size: 4.8rem;
    font-weight: bold;
    text-align: center;
    line-height: 1em;
    margin-bottom: 13rem;
  }
  &-list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__logo {
      width: auto;
      max-height: 4rem;
    }
  }
}
.swiper {
  position: relative;  
  &-slide {
    display: inline-flex;    
    height: auto;
    .logo { 
      width: 80%;
    }
  }
}
</style>
