<template>
  <section class="news l-section">
    <div class="inner">
      <h3 class="l-section__title"> </h3>
      <div data-loding-spinner="true">
        <ul class="news-list" :data-list-index="listCurrentNum">
          <li class="news-list__item" v-for="item in this.listDatas">
            <a class="news-list__link" :href="`${item.mn_url}`" target="_blank">
              <span class="txt clamp-1">{{ item.mn_title }}</span>
              <span class="date">{{ item.post_date }}</span>
            </a>
          </li>
        </ul>
        <ol class="news-pagination" :data-pagination-index="pagingCurrentNum">
          <li class="news-pagination__item news-pagination__item--prev" :class="{ 'is-disable': !isPagePrev }">
            <button class="news-pagination__btn" @click="getCurrentData(--pagingCurrentNum * pagingCount - (pagingCount - 1))" type="button"></button>
          </li>
          <li class="news-pagination__item" v-for="num in currentPages" :ref="setListItemRef">
            <button class="news-pagination__btn" @click="getCurrentData(num)">
              {{ num }}
            </button>
          </li>
          <li class="news-pagination__item news-pagination__item--next" :class="{ 'is-disable': !isPageNext }">
            <button class="news-pagination__btn" @click="getCurrentData(pagingCurrentNum++ * pagingCount + 1)" type="button"></button>
          </li>
        </ol>
      </div>
    </div>
  </section>
</template>

<script>
import axios from "axios";

export default {
  name: "news",
  data() {
    return {
      listDatas: null,
      listItemRefs: [],
      listCount: 4, //    
      listCurrentNum: null, //    
      pagingTotals: null,
      pagingCount: 4, //  button 
      pagingCurrentNum: 1, //   
      get isPagePrev() {
        return this.pagingTotals === null ? false : this.pagingCurrentNum > 1;
      },
      get isPageNext() {
        return this.pagingTotals === null ? false : this.pagingCurrentNum < this.pagingTotals.length;
      },
      get currentPages() {
        return this.pagingTotals === null ? null : this.pagingTotals[this.pagingCurrentNum - 1];
      },
    };
  },
  created() {
    this.getPageTotal();
    this.getCurrentData(this.pagingCurrentNum);
  },
  methods: {
    /* API -    */
    getPageTotal() {
      axios
        .get(`${SERVER.api}/api/contact/news/getMainNewsInfoTotalCnt`)
        .then((_response) => {
          const totalCount = _response.data.newsInfoTotalCnt;

          this.pagingTotals = Array.from({ length: totalCount / this.listCount }, (v, i) => i + 1).division(this.pagingCount);
        })
        .catch((_error) => {
          console.log(_error);
        });
    },

    /* API -   */
    getCurrentData(_pageNum) {
      axios
        .post(`${SERVER.api}/api/contact/news/getMainNewsInfo?pageNum=${_pageNum - 1}`)
        .then((_response) => {
          this.listDatas = _response.data.newsInfo;
          this.listCurrentNum = _pageNum;
        })
        .catch((_error) => console.log(_error))
        .finally(() => this.$el.querySelector("[data-loding-spinner]").setAttribute("data-loding-spinner", false));
    },
    setListItemRef(el) {
      this.listItemRefs.push(el);
    },
  },
  updated() {
    /* pagination - active toggle */
    const $lis = this.listItemRefs;

    $lis.forEach((_$li) => {
      if (Boolean(_$li) === false) return;

      const $btn = _$li.querySelector("button");
      const btnNum = Number($btn.innerHTML);
      const itemNum = this.listCurrentNum;

      if (btnNum === itemNum) _$li.classList.add("is-active");
      else _$li.classList.remove("is-active");
    });
  },
};
</script>

<style lang="scss" scoped>
.news {
  background-color: #f8fafc;
  &-list {
    margin-bottom: 5.2rem;
    &:empty {
      &::after {
        [data-loding-spinner="false"] & {
          content: " .";
          display: flex;
          justify-content: center;
          align-items: center;
          height: 10rem;
          padding: 0 3.5rem;
          font-size: 2.3rem;
          font-weight: bold;
          color: #ccc;
          border-radius: $G-box-radius;
          background-color: #fff;
          box-shadow: 0 0 0.8rem rgba(7, 7, 7, 0.08);
        }
      }
    }
    &__item {
      padding: 1rem 0;
    }
    &__link {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 10rem;
      padding: 0 3.5rem;
      font-size: 2.3rem;
      color: #505050;
      border-radius: $G-box-radius;
      background-color: #fff;
      box-shadow: 0 0 0.8rem rgba(7, 7, 7, 0.08);
      &:hover {
        color: #050505;
      }
      .txt {
        width: 85%;
      }
      .date {
        font-size: 2rem;
        color: #a3a3a3;
      }
    }
  }
  &-pagination {
    $p: &;

    display: flex;
    justify-content: center;
    &__item {
      padding: 0 1rem;
    }
    &__btn {
      font-size: 2rem;
      font-weight: 500;
      color: #b4b4b4;
      @include transition;

      .is-active & {
        color: #6794f7;
      }
      .is-disable & {
        pointer-events: none;
        opacity: 0.3;
      }
    }
  }
}
</style>
