<template>
  <footer id="footer" class="footer" role="contentinfo">
    <div class="footer-overlay"></div>
    <div class="footer-inner">
      <div class="inner">
        <div class="about">
          <LinkLogo></LinkLogo>
          <div class="about-list">
            <a class="about-list__link" @click="isShow('popupTerms')"> </a>
            <a class="about-list__link" @click="isShow('popupPersonal')"> </a>
            <a class="about-list__link" @click="isShow('popuprRefusal')"></a>
          </div>
        </div>
        <div class="contact">
          <p class="contact-address">   109   28  2801</p>
          <div class="contact-list">
            <p class="contact-list__txt">Tel. <a href="tel:0263703732">02 6370 3732</a></p>
            <p class="contact-list__txt">Fax. <a href="tel:0263703798">02 6370 3798-9</a></p>
            <p class="contact-list__txt">Email. <a href="mailto:p.esg@prone.co.kr">p.esg@prone.co.kr</a></p>
          </div>
          <p class="contact-corpyright"> P-ESG Corp. All rights reserved.</p>
        </div>
      </div>
    </div>
    <div class="footer-bg"></div>
  </footer>
</template>

<script>
import * as eventBus from "@/eventBus.js";
import LinkLogo from "../buttons/LinkLogo.vue";

export default {
  components: {
    LinkLogo,
  },
  data() {
    return {
      $container: "",
    };
  },
  watch: {
    $route() {
      this.scrollingAction();
    },
  },
  methods: {
    isShow(_key) {
      eventBus[_key].$emit("isShow", "true");
    },
    scrollingAction() {
      /* Footer   */
      const $footer = document.querySelector("#footer");
      let status = true;

      document.addEventListener("scroll", () => {
        const $container = document.querySelector("#container");
        const containerH = $container.offsetHeight;
        const scrollBottom = window.scrollY + window.innerHeight;
        const footerH = $footer.offsetHeight;

        if (scrollBottom >= containerH) {
          let posT = Math.abs(((scrollBottom - containerH) / footerH) * 1);
          let Y = posT >= 1 ? 1 : posT;

          if (status) {
            $footer.querySelector(".footer-overlay").style.opacity = Math.abs(Y - 1);
            $footer.querySelector(".inner").style.transform = "translateY(" + Math.abs(Y * 100 - 100) + "px)";
          }
          status = true;
        } else status = false;
      });

      $footer.querySelector(".inner").style.transform = "translateY(0)";
      $footer.querySelector(".footer-overlay").style.opacity = 0;
    },
  },
  created() {
    //
  },
  mounted() {
    this.scrollingAction();
  },
  updated() {
    //
  },
};
</script>

<style lang="scss" scoped>
/* footer */
.footer {
  $footerH: 37.5rem;

  position: relative;
  height: $footerH;
  &-inner {
    position: fixed;
    bottom: 0;
    width: 100%;
    min-width: $G-inner-width;
    height: $footerH;
    transform: translateY(10rem);
  }
  &-overlay {
    position: fixed;
    bottom: 0;
    z-index: 2;
    width: 100%;
    height: $footerH;
    background-color: #ccc;
    pointer-events: none;
  }
  &-bg {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
    background-color: #f9f9f9;
  }
}

// 
.about {
  display: flex;
  justify-content: space-between;
  padding-bottom: 1.5rem;
  margin-bottom: 6.2rem;
  border-bottom: 0.1rem solid #d0d1e5;
  .logo {
    font-size: 3.6rem;
  }
  &-list {
    display: flex;
    align-items: center;
    &__link {
      margin-left: 6rem;
      font-size: 1.5rem;
      font-weight: bold;
      color: #4d4d5b;
    }
  }
}

// 
.contact {
  font-size: 1.5rem;
  color: #4d4d5b;
  text-align: center;
  &-address {
    margin-bottom: 0.5rem;
  }
  &-list {
    display: inline-flex;
    &__txt {
      position: relative;
      margin-bottom: 2rem;
      padding: 0 1rem;
      letter-spacing: 0.02em;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        height: 1.1rem;
        width: 0.1rem;
        background-color: #4d4d5b;
        transform: translateY(-50%);
      }
      &:first-child::after {
        display: none;
      }
    }
  }
}
</style>
